<template>
  <div class="articles">
    <ul>
      <li v-for="item in contents[this.dayData].children" :key="item.id">
        <router-link :to="{ name: 'contents', params: { id: item.id}}">
          <img :src="item.img" alt="">
          <div class="articles-nei">
            <h2 class="articles-title">{{item.h1}}</h2>
            <p class="articles-text">{{item.text}}</p>
            <p class="articles-name">王安安 <span class="articles-time">2017-10-24</span></p>
            <i class="el-icon-star-off articles-love"></i>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import { contents } from "~/data/index";

export default {
  data() {
    return {
      contents: contents
    };
  },
  props: {
    dayData: {}
  },
  created() {},
  components: {}
};
</script>

<style scoped lang="scss">
ul {
  li:hover {
    box-shadow: 0px 3px 3px #dddddd;
    .articles-title {
      color: #242299;
      border-bottom: 2px solid #242299;
    }
  }
  li:nth-child(1) {
    margin-top: 0;
  }
  li {
    padding: 20px;
    margin-top: 20px;
    background: #fff;
    transition: 0.3s;
    box-shadow: 0px 0px 0px #dddddd;
    a {
      color: #333;
    }
    img {
      float: left;
      width: 230px;
      height: 140px;
      margin-right: 15px;
    }
    .articles-title {
      margin-bottom: 8px;
      font-weight: 350;
      transition: 0.2s;
      border-bottom: 2px solid transparent;
      display: inline-block;
    }
    .articles-text {
      line-height: 175%;
      color: #666666;
      margin-top: 5px;
      text-align: justify;
      height: 50px;
      overflow: hidden;
      margin-bottom: 25px;
    }
    .articles-name {
      color: #242299;
      font-size: 13px;
      display: inline-block;
      .articles-time {
        display: inline-block;
        height: 24px;
        line-height: 24px;
        color: #999999;
      }
    }
    .articles-love {
      float: right;
      font-size: 18px;
    }
  }
}
</style>



