<template>
  <div class="products">
    <div class="illness-t animated fadeInDown delay_0">
        <img :src="this.contents[this.data].img" alt="">
        <div class="illness-l">
            <h3>{{ this.contents[this.data].label }}</h3>
            <p>{{ this.contents[this.data].p }}</p>
        </div>
    </div>
    <div class="homeContent-center">
      <mainLeft class="animated fadeInLeft delay_100"></mainLeft>
      <div class="mainRight2 animated fadeInRight delay_100">
        <articles :dayData="this.data"></articles>
        <div class="main-more">
          <el-button type="primary">加载更多</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { products } from "~/data/index";
import { contents } from "~/data/index";
import mainLeft from "~/components/mainLeft.vue";
import articles from "~/components/articles.vue";

export default {
  data() {
    return {
      data: {},
      products: products,
      contents: contents
    };
  },
  methods: {
    history() {
      history.go(-1);
    }
  },
  created() {
    this.data = this.$route.params.id;
    window.scrollTo(0, 0);
  },
  components: {
    mainLeft,
    articles
  }
};
</script>

<style lang="scss" scoped>
.products {
  width: 1200px;
  margin: 0 auto;
  margin-bottom: 100px;
  h1 {
    width: 100%;
    padding: 35px 0;
    font-weight: 400;
    span {
      float: right;
      font-size: 18px;
      line-height: 18px;
      cursor: pointer;
      padding: 13px 0;
      color: #03a57b;
    }
  }
  .illness-t:after {
    content: "\0020";
    display: block;
    height: 0;
    clear: both;
  }
  .illness-t {
    width: 100%;
    background: #fff;
    margin-bottom: 30px;
    img {
      float: left;
      width: 400px;
    }
    .illness-l {
      overflow: hidden;
      padding: 0 50px;
      h3 {
        margin-bottom: 20px;
        margin-top: 75px;
        font-size: 23px;
        letter-spacing: 3px;
      }
      p {
        opacity: 0.6;
        line-height: 23px;
        line-height: 29px;
      }
    }
  }
}

.mainRight2 {
  overflow: hidden;
}
.mainRight-top {
  width: 900px;
  background: #fff;
  display: flex;
  padding: 0 30px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.15);
}

.main-more {
  text-align: center;
  padding: 30px;
  button {
    background: linear-gradient(90deg, #3369e7, #242299);
    color: #fff;
    box-shadow: 0 2px 6px 0 rgba(51, 105, 231, 0.4);
    border: 0;
    border-radius: 999em;
  }
}
</style>



